:root {
    --light-red:#EB3434;
    --med-red: #8C1F28;
    --dark-red: #591C21;
    --light-yellow: #F9B317;
    --dark-green: #044040;
    --main-pad: 130px;
    --thin-pop: 500;
    --bold-pop:700;
    --thin-comic: 300;
}

/* transitions */
@keyframes slideInFromLeft {
    0% {
      transform: translateX(-1000px);
    }
    100% {
      transform: translateX(0);
    }
}
@keyframes slideInFromRight {
    0% {
      transform: translateX(1000px);
    }
    100% {
      transform: translateX(0);
 
    }
}


@keyframes fadeIn {
    from {
        opacity: 0;
    }
    to {
       opacity: 1;
    }
  }
/* end transitions */

/* Global */
.btn {
    outline: None;
    border: transparent 4.5px solid;
    border-radius: 26px;
    color: #fff;
    font-size: 20px;
    transition:  border-color 400ms ease,background-color 400ms ease ,transform 300ms ease,color 300ms ease;
}
.btn:hover {
    border-color: var(--light-red);
    background-color: transparent;
    cursor: pointer;
}
.green {
    color:var(--light-yellow);
}
.red {
    color: var(--light-red);
}
.comic-thin{
    font-family: 'Comic Neue', cursive;
    font-weight: var(--thin-comic);
}
.comic-bold{
    font-family: 'Comic Neue', cursive;
    font-weight: var(--bold-pop);
}
.app {
    height: 100%;
}
.container {
    padding: 0 15px;
    margin:0 auto;

}
  /* Small */
  @media (min-width: 768px) {
    .container {
      width: 750px;
    }
}
/* Medium */
@media (min-width: 992px) {
    .container {
      width: 970px;
    }
}
/* Large */
@media (min-width: 1200px) {
    .container {
      width: 1170px;
    }
}

.active {
    color: var(--light-red);
}
ul {
    list-style: none;
}
a {
    text-decoration: none;
    color: inherit;
}
.container .hide div, .hide {
    display: none;
}
/* End Global */

/* Header */

#header {
    position: sticky;
    top: 0;
    z-index: 2;
    width: 100%;
    height: 10vh;
    background-color: #333;
}

#header .container {
    display: flex;
    height: 100%;
    justify-content: space-between;
    align-items: center;
    padding: 0px 35px ;
}
#header .logo {
    cursor: pointer;
}
#header .container ul {
    display: flex;
}

ul li {
    margin-left: 81px;
    transition: color 200ms ease-in-out;
    font-size: 18px;
    cursor: pointer;
}
ul li:hover {
    color: var(--light-red)
}
nav.mobile {
    position: fixed;
    animation: slideInFromRight 300ms ease-out 0s 1;
    top: 0;
    left: 0;
    height: 100vh;
    z-index: 999;
    width: 100%;
    background-color: #333;
    padding-bottom: 70px;
}
nav.mobile ul  {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    height: 80%;
    position: relative;
    top:100px;
    width: 100%;
    align-items: center ;
}
nav.mobile li {
    margin-left: 0;

}
nav.mobile i {
    position: absolute;
    top: 25px;
    right: 25px;
    font-size: 26px;
    transition: color 250ms ease-in-out;
    cursor: pointer;
}
nav.mobile i:hover{ 
    color: var(--light-red);
}
.header-vect {
    position: absolute;
    left:10%;
    top: calc(100% - 10px);
    transform: rotate(-0.43deg);
    animation: fadeIn 1s ease-in 0s 1;
    background-color: #333;
    width:80%;
}
img.header-vect ::before {
    content: '';
    width: 50px;
    height: 28px;
    background-color: #333;
}
.hamburger i {
    font-size: 26px;
}

@media screen and (max-width: 820px  ) {
   #header .container ul li {
    margin-left: 55px;
   }
   #header .header-vect {
    width:100%;
    left:0;

   }
}
/* end Header */


/* Hero */

#hero {
    padding-top: var(--main-pad);
    width: 100%;
}
#hero .container {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    align-items: center;
    gap: 55px;

}

#hero .text{
    font-size: 30px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    animation: slideInFromLeft 1s ease-in 0s 1 ;  
    padding-bottom: 50px;
}
#hero .text .name{
    margin-bottom: 20px;
}
#hero .text .niche{
    margin-bottom: 55px;
    max-width: 20ch;
}
#hero .socials {
    display: flex;
    gap: 20px;
    margin-bottom: 40px;
}
.socials .social-icon {
    width: 40px;
    height: 40px;
    transition: transform 250ms ease-in;
}
.socials .social-icon:hover {
    transform: scale(1.1);
}
#hero .portrait {
    animation: slideInFromRight 1s ease-in 0s 1 ;
}
#hero .portrait img {
    width: 450px;
}
@media screen and (max-width: 1000px) {
    #hero .portrait img {
        width: 350px;
    }
    #hero .text {
        font-size: 22px;
    }
    #hero .btn {
        font-size: 16px;
    }
}
@media screen and (max-width: 700px) {
    #hero .container {
        justify-content: center;
    }
    #hero .text h1{
        font-size: 46px;
        text-align: center;

    }
    #hero .text h4.niche {
        font-size: 32px;
        margin-bottom: 160px;
        text-align: center;
    }
    #hero .text .cta-btn {
        width: 100%;
        font-size: 22px;
    }
    #hero .socials {
        width: 100%;
        justify-content: center;
    }
    .social-icon {
        width: 45px;
        height: 45px;
    }
    .portrait {
        text-align: center;
    }
}
.cta-btn {
    width: 50%;
    height: 60px;
    font-weight: 700;
    background-color: var(--med-red);
}

/* end Hero */

/* About */
#About {
    padding: calc( var(--main-pad) - 20px ) 0;
    width: 100%;
}
#About .container {
    display: grid;
    place-items: center;
}
#About .text {
    text-align: center;
    max-width: 100%;
}
#About .text h1 {
    margin-bottom: 45px;
    font-size: 5.25rem;
}
#About .text h4 {
    margin-bottom: 22px;
    font-size: 1.5rem;
}
#About .text p {
    font-size: 1.5rem;
    margin-bottom: 45px;

}
#About .tabs {
    display: flex;
    justify-content: flex-start;
    gap: 24px;
    width: 100%;
    padding: 20px 0 30px;
}
#About .tabs .tablink {
    height: 53px;
    text-align: center;
    background-color: transparent;
    border: 4px solid var(--light-yellow);
    color: var(--light-yellow);
    border-radius: 54px;
    padding: 13px 40px;
    font-size: 16px;
    font-weight: 700;
    transition: transform 250ms ease-out;
}
#About .tabs .tablink:hover {
    transform: scale(1.05);

}
#About .tabs .active {
    color: #333;
    background-color: var(--light-yellow);
    border:0;
}
#About .tabcontent {
    display: grid;
    text-align: center;
    width: 100%;
    grid-template-columns: repeat(auto-fit,minmax(150px,1fr));
    grid-gap: 20px;
}
 @media screen and (max-width: 820px) {
    :root {
        font-size: 14.5px;
    }
    #About .text {
       padding: 24px 0;
    }
    #About .tabs .tablink {
        padding: 13px 25px;

    }
}


#About .card {
    background-color: #fff;
    color: #333;
    text-align: center;
    border-radius: 13.64px;
    padding: 25px 30px;
    transition: transform 250ms ease-in-out , background-color 250ms ease-in-out,color 250ms ease-in-out;
}
#About .card:hover {
    color: #333;
    background-color: var(--light-yellow) ;

}
#About .card:hover {
    transform: scale(1.05);
}
#About .card img {
    width: 64px;
}
#About .card h2 {
    font-size: 16px;
    font-weight: 700;
    margin-top: 8px;

}
#About #coding h2 {
    font-size: 14px;
}


/*  Portfolio */

#Portfolio {
    padding: calc( var(--main-pad) ) 0;
    width: 100%;
}
#Portfolio .container {
    text-align: center;
}
#Portfolio h1 {
    font-size: 5.25rem;
}
#Portfolio p {
    margin-top: 20px;
    font-size: 1.5rem;
    margin-bottom: 95px;
    font-weight: var(--thin-comic);

}
#Portfolio .port-items {
    display: grid;
    grid-template-columns: repeat(auto-fit,minmax(45% ,1fr));
    grid-auto-rows: auto;
    column-gap: 6%;
    width: 100%;

}
#Portfolio .port-item  {
    margin-bottom: 6%;
    text-align: left;

}
#Portfolio .cover {
    max-width: 100%;
    aspect-ratio: 16 / 9;
    margin-bottom: 12px;
    transition: transform 300ms ease-in-out;
    cursor: pointer;
}
#Portfolio .port-item div {
    transition: transform 300ms ease-in-out;
}

#Portfolio .cover:hover ,#Portfolio .cover:hover + div{ 
    transform: scale(1.03);
}
#Portfolio .port-item .title {
    letter-spacing: 2px;
    font-size: 27px;
    margin-bottom: 4px;
}
#Portfolio .categories {
    font-size: 14px;
    font-weight: var(--thin-comic);
    margin-bottom: 25px;
}
#Portfolio .categories > span {
    transition: color 250ms ease-in-out;
}
#Portfolio .categories > span:hover {
    color: var(--light-yellow);
}
#Portfolio .btn{
    background-color: var(--med-red);
    font-weight: var(--bold-pop);
    border-radius: 15px;
    padding: 8px 17px;
    font-size: 15px;
    margin-right: 20px;
    cursor: pointer;    
}
#Portfolio .btn:hover {
    background-color: transparent;
    transform: scale(1.05);
}
#Portfolio .port-item .btn:nth-child(even):hover{
    color: var(--light-yellow);
}
#Portfolio .btn.last{
    margin-top: 30px;
    font-size: 18px;
    align-self: center;
    background-color: var(--light-yellow);
    color: #333;
    
}
#Portfolio .btn.last:hover {
    border-color: var(--light-yellow);
    background-color: transparent;
    color: #fff;
}
@media screen and (max-width : 760px) {
    #Portfolio .port-items {
        grid-template-columns: 1fr;
        grid-gap: 50px;
    }
}

